<template><section><html><head></head><body><h1>LabelCollection</h1>
<p><code v-pre="">vc-collection-primitive-label</code> 组件用于加载标签图元集合，标签是场景中与视口对齐的文本。</p>
<h2>示例</h2>
<h3>加载标签图元集合</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-label :labels="labels" @click="clicked"></vc-collection-primitive-label>
        <vc-collection-primitive-label>
          <template v-for="(polyline, index) of polylines">
            <vc-primitive-label @click="clicked" :position="polyline.positions[polyline.positions.length-1]" :key="'label'+index" :text="'面积'+(polyline.area > 1000000 ? (polyline.area / 1000000).toFixed(2) + 'km²' : polyline.area.toFixed(2) + '㎡')" show-background="" :horizontal-origin="1">
            </vc-primitive-label>
          </template>
        </vc-collection-primitive-label>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
