<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 4x4 变换矩阵，将每个点从模型转换为世界坐标。</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定是否显示此图元的 BoundingVolume， 仅调试使用。</td>
</tr>
<tr>
<td>blendOption</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定颜色混合选项。</td>
</tr>
<tr>
<td>scene</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 必须传递使用高度参考属性的标签，否则将针对地球进行深度测试。</td>
</tr>
<tr>
<td>labels</td>
<td>Array</td>
<td><code v-pre="">[]</code></td>
<td><code v-pre="">optional</code> 指定标签集合数组。 数组对象结构为<a href="./#/zh/primitive/vc-primitive-label"><code v-pre="">vc-primitive-label</code></a>组件属性。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/LabelCollection.html">LabelCollection</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元集合上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元集合上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元集合时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元集合外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该图元集合时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元集合上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该图元集合时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该图元集合时触发。</td>
</tr>
</tbody>
</table>
<hr>
